.section {
  padding-top: 110px;
  padding-bottom: 110px;
  @include cover;
  @include xl {
    padding-top: 80px;
    padding-bottom: 80px; }
  @include lg {
    padding-top: 60px;
    padding-bottom: 60px; }
  @include md {
    padding-top: 50px;
    padding-bottom: 50px; }
  @include sm {
    padding-top: 30px;
    padding-bottom: 30px; }
  &__title {
    text-align: center;
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 20px; }
    @include md {
      margin-bottom: 15px; } }
  &__btn {
    text-align: center; }

  &--sm {
    padding-top: 45px;
    padding-bottom: 45px;
    @include xl {
      padding-top: 45px;
      padding-bottom: 45px; }
    @include lg {
      padding-top: 45px;
      padding-bottom: 45px; }
    @include md {
      padding-top: 45px;
      padding-bottom: 45px; }
    @include sm {
      padding-top: 30px;
      padding-bottom: 30px; } } }

.s-order {
  @include md {
    background-image: none !important; }
  &__text-block {
    max-width: 520px;
    @include md {
      text-align: center;
      margin-left: auto;
      margin-right: auto; } }
  &__features {
    @include md {
      margin-bottom: 30px; } } }
