.article {
  color: $c-title;
  text-align: justify;
  @include clr;
  p, h1, h2, h3, h4, h5, h6, ul, ol {
    @include notlast {
      margin-bottom: 25px;
      @include md {
        margin-bottom: 15px; } } }
  img {
    float: right;
    margin-left: 30px;
    margin-bottom: 30px;
    @include md {
      width: 100%;
      float: none;
      margin-left: 0;
      margin-bottom: 15px; } } }
