.service-link {
  @include flex(flex-end, normal, column);
  padding: 100px 30px 30px;
  margin-bottom: 30px;
  height: calc(100% - 30px);
  color: $white;
  @include cover;
  @include lg {
    padding: 100px 20px 20px; }
  @include md {
    height: calc(100% - 15px);
    margin-bottom: 15px; }
  @include sm {
    padding: 100px 15px 15px; }
  @include xs {
    height: auto; }
  &__content {
    display: block; }
  &__title {
    display: block;
    margin-bottom: 10px;
    @include md {
      margin-bottom: 5px; } }
  &__text {
    display: block;
    margin-bottom: 10px;
    max-width: 320px;
    font-size: $fz-sm;
    @include md {
      margin-bottom: 5px; } } }
